import React, { useEffect, useState } from "react";

import { Button, Card, Col, Form, Row } from "@themesberg/react-bootstrap";

const UploadButton = ({
  onChange,
  currentFile,
  uploadFile,
  deleteFile,
  isLoading,
}) => {
  if (currentFile) {
    if (currentFile instanceof File) {
      return (
        <div>
          <div>
            <p>File selected: {currentFile.name}</p>
          </div>
          <div className="d-flex">
            <Button
              className="me-3"
              size="sm"
              onClick={uploadFile}
              disabled={isLoading}
            >
              Upload this file
            </Button>
            <Button
              size="sm"
              variant="danger"
              onClick={() => onChange(null)}
              disabled={isLoading}
            >
              Remove this file
            </Button>
          </div>
        </div>
      );
    } else if (currentFile.url && currentFile.name) {
      return (
        <div>
          <p>
            {currentFile.name} :{" "}
            <a target="_blank" rel="noopener noreferrer" href={currentFile.url}>
              Open
            </a>
          </p>
          <div>
            <Button size="sm" onClick={deleteFile}>
              Delete this file
            </Button>
          </div>
        </div>
      );
    }
  }

  return (
    <Card>
      <input type="file" onChange={onChange} />
    </Card>
  );
};

const ResumeUpload = ({ resumes, uploadFn, deleteFn }) => {
  const [selectedOption, setOption] = useState(0);
  const [currentResume, setCurrentResume] = useState("");
  const [isLoading, setLoading] = useState(false);

  const handleFileChange = (e) => {
    const file = e?.target?.files[0];
    if (!file) {
      setCurrentResume("");
      return;
    }

    setCurrentResume(file);
  };

  const handleOptionChange = (e) => {
    const index = e.target.value;
    setOption(index);
  };

  const uploadFile = async () => {
    setLoading(true);
    await uploadFn(currentResume, selectedOption);
    setLoading(false);
  };

  const deleteFile = async () => {
    setLoading(true);
    await deleteFn(currentResume._id);
    setLoading(false);
  };

  useEffect(() => {
    setCurrentResume(resumes[selectedOption] || "");
  }, [resumes, selectedOption]);

  return (
    <div>
      <h4>Upload your resume</h4>
      <Card className="p-4">
        <Row>
          <Col lg={4}>
            <Form.Select
              disabled={isLoading}
              value={selectedOption}
              onChange={handleOptionChange}
            >
              <option value={0}>Resume 1</option>
              <option value={1}>Resume 2</option>
              <option value={2}>Resume 3</option>
            </Form.Select>
          </Col>
          <Col lg={12}>
            <div className="mt-3">
              <UploadButton
                currentFile={currentResume}
                onChange={handleFileChange}
                uploadFile={uploadFile}
                deleteFile={deleteFile}
                isLoading={isLoading}
              />
            </div>
          </Col>
        </Row>
      </Card>
    </div>
  );
};

export default ResumeUpload;
